import $ from "jquery";
import React, { useEffect, useState } from "react";
import { Container, Nav } from "react-bootstrap";
import { useTranslation } from "react-i18next";
import { IMG_BASE_URL } from "../constants";
import SocialNetworks from "../containers/SocialNetworks";
import "./css/Navigation.css";
import LanguageSelector from "./LanguageSelector";

const CODING_ICON = `${IMG_BASE_URL}/logos/coding.png`;
const TERMINAL_ICON = `${IMG_BASE_URL}/logos/terminal.png`;

export let routes: Array<{
  key: number;
  translationNameKey: string;
  name: string;
  link: string;
}> = [
    {
      key: 1,
      translationNameKey: "nav_home",
      name: "Home",
      link: "#home",
    },
    {
      key: 2,
      translationNameKey: "nav_about",
      name: "About",
      link: "#about",
    },
    {
      key: 3,
      translationNameKey: "nav_works",
      name: "Works",
      link: "#work",
    },
    {
      key: 4,
      translationNameKey: "nav_resume",
      name: "Resume",
      link: "#resume",
    },
    {
      key: 5,
      translationNameKey: "nav_contact",
      name: "Contact",
      link: "#contact",
    },
  ];

function Navigation() {
  const { t } = useTranslation();
  const [logo, setLogo] = useState<string>(CODING_ICON);

  useEffect(() => {
    const nav = $("#mainNav");
    let navHeight = nav.outerHeight();

    $(".navbar-toggler").on("click", () => {
      if (!$("#mainNav").hasClass("navbar-reduce")) {
        $("#mainNav").addClass("navbar-reduce");
      }
    });

    ($("body") as any).scrollspy({
      target: "#mainNav",
      offset: navHeight,
    });

    $(".js-scroll").on("click", () => {
      ($(".navbar-collapse") as any).collapse("hide");
    });

    window.addEventListener("scroll", () => {
      if (window.pageYOffset > 50) {
        document
          .querySelector(".navbar-expand-md")
          ?.classList.add("navbar-reduce");
        document
          .querySelector(".navbar-expand-md")
          ?.classList.remove("navbar-trans");
        setLogo(TERMINAL_ICON);
      } else {
        document
          .querySelector(".navbar-expand-md")
          ?.classList.add("navbar-trans");
        document
          .querySelector(".navbar-expand-md")
          ?.classList.remove("navbar-reduce");
        setLogo(CODING_ICON);
      }
    });

    $(".js-scroll").on("click", () => {
      ($(".navbar-collapse") as any).collapse("hide");
    });
  }, []);

  return (
    <Nav
      className="navbar navbar-b navbar-trans navbar-expand-md fixed-top"
      id="mainNav"
      style={{ height: "60px" }}
    >
      <Container>
        <a className="navbar-brand js-scroll" href="/">
          <img src={logo} alt="logo" style={{ maxWidth: "50px" }} />
        </a>
        <button
          className="navbar-toggler collapsed"
          type="button"
          data-toggle="collapse"
          data-target="#navbarDefault"
          aria-controls="navbarDefault"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span></span>
          <span></span>
          <span></span>
        </button>
        <div
          className="navbar-collapse collapse justify-content-end"
          id="navbarDefault"
        >
          <ul className="navbar-nav">
            {routes.map((r, i) => {
              return (
                <li className="nav-item" key={i}>
                  <a
                    key={r.key}
                    href={r.link}
                    className={
                      i === 0
                        ? "nav-link js-scroll active"
                        : "nav-link js-scroll"
                    }
                  >
                    {t(r.translationNameKey)}
                  </a>
                </li>
              );
            })}
            <LanguageSelector />
          </ul>
        </div>

        <div className="navbar-brand js-scroll" style={{ marginLeft: "50px" }}>
          <SocialNetworks />
        </div>
      </Container>
    </Nav>
  );
}

export default Navigation;
