import React from "react";
import { IMG_BASE_URL } from "../constants";

type ITechnoProps = {
  class: string;
  id: number;
  name: string;
};

function Techno(props: ITechnoProps) {
  // azure icon hack
  if (props.name.toLowerCase() === "azure") {
    return (
      <li className="list-inline-item mx-3" key={props.id}>
        <img
          src={`${IMG_BASE_URL}/icons/azure.png`}
          alt="icon techno"
          style={{ width: "50px", height: "50px" }}
        />
      </li>
    );
  }

  return (
    <li className="list-inline-item mx-3" key={props.id}>
      <span>
        <div className="text-center skills-tile">
          <i className={props.class} style={{ fontSize: "400%" }}></i>
        </div>
      </span>
    </li>
  );
}
export default Techno;
