import i18next from "i18next";
import React, { useEffect } from "react";
import { Button } from "react-bootstrap";
import { IMG_BASE_URL } from "../constants";

const languageMap: any = {
  en: { label: "English", dir: "ltr", active: true },
  fr: { label: "FranÃ§ais", dir: "ltr", active: false },
};

function LanguageSelector() {
  const selected = localStorage.getItem("i18nextLng") || "en";

  useEffect(() => (document.body.dir = languageMap[selected].dir), [selected]);

  const changeLanguage = (key: string) => i18next.changeLanguage(key);

  return (
    <div>
      <Button variant="lg" onClick={() => changeLanguage("fr")}>
        <img src={`${IMG_BASE_URL}/logos/france.png`} alt="fr" />
      </Button>
      <Button variant="lg" onClick={() => changeLanguage("en")}>
        <img src={`${IMG_BASE_URL}/logos/uk.png`} alt="en" />
      </Button>
    </div>
  );
}

export default LanguageSelector;
