import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useTranslation } from "react-i18next";
import { VerticalTimeline } from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import Experience from "../components/Experience";

type IExperienceItem = {
  id: number;
  title: string;
  location: string;
  description: string;
  date: string;
  icon: string;
  logo: string;
  details: string[];
};

function Resume() {
  const { t } = useTranslation();
  let experiences: IExperienceItem[] = t("resume_experiences", {
    returnObjects: true,
  });

  return (
    <section
      id="resume"
      className="resume-mf bg-image sect-pt4 route"
      style={{ backgroundImage: 'url("/logos/reflection.jpg")' }}
    >
      <div id="stars" />
      <div id="stars2" />
      <div id="stars3" />
      <Container>
        <Row>
          <Col sm={12}>
            <div className="title-box text-center">
              <h3 className="title-a" style={{ color: "white" }}>
                {t("resume_title")}
              </h3>
              <div className="line-mf"></div>
            </div>
          </Col>
        </Row>
        <Row>
          <VerticalTimeline>
            {experiences.map((d, i) => {
              return (
                <Experience
                  id={i.toString()}
                  key={d.id}
                  title={d.title}
                  date={d.date}
                  location={d.location}
                  description={d.description}
                  icon={d.icon}
                  logo={d.logo}
                />
              );
            })}
          </VerticalTimeline>
        </Row>
      </Container>
    </section>
  );
}

export default Resume;
