import React, { useEffect } from "react";
import "./css/Preloader.css";
import $ from "jquery";

function Preloader() {
  useEffect(() => {
    $(window).on("load", function () {
      if ($("#preloader").length) {
        $("#preloader")
          .delay(100)
          .fadeOut("slow", function () {
            $(this).remove();
          });
      }
    });
  }, []);

  return <div id="preloader"></div>;
}

export default Preloader;
