import React from "react";
import { VerticalTimelineElement } from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import { ReactComponent as SchoolIcon } from "../assets/icons/school.svg";
import { ReactComponent as WorkIcon } from "../assets/icons/work.svg";
import "./css/Experience.css";

type IExperienceProps = {
  title: string;
  icon: string;
  description: string;
  id: string;
  date: string;
  logo: string;
  location: string;
};

function Experience(props: IExperienceProps) {
  let workIconStyles = { background: "#06D6A0" };
  let schoolIconStyles = { background: "#f9c74f" };
  let isWorkIcon = props.icon === "work";

  return (
    <VerticalTimelineElement
      key={props.id}
      date={props.date}
      dateClassName="date"
      iconStyle={isWorkIcon ? workIconStyles : schoolIconStyles}
      icon={isWorkIcon ? <WorkIcon /> : <SchoolIcon />}
    >
      <img className="companyImg" src={props.logo} alt="company logo" />
      <h3 className="vertical-timeline-element-title item-h3">{props.title}</h3>
      <h5 className="vertical-timeline-element-subtitle">{props.location}</h5>
      <p id="description">{props.description}</p>
    </VerticalTimelineElement>
  );
}

export default Experience;
